// Базовые цвета https://colorscheme.ru/color-converter.html

$black:                       hsl(0, 0%, 0%);
$gray-darkest:                hsl(0, 0%, 10%);
$gray-darker:                 hsl(0, 0%, 20%);
$gray-darken:                 hsl(0, 0%, 30%);
$gray-dark:                   hsl(0, 0%, 40%);
$gray:                        hsl(0, 0%, 50%);
$gray-light:                  hsl(0, 0%, 60%);
$gray-lighten:                hsl(0, 0%, 70%);
$gray-lighter:                hsl(0, 0%, 80%);
$gray-lightest:               hsl(0, 0%, 90%);
$white:                       hsl(0, 0%, 100%);

$color-success:               hsl(120, 39%, 54%);
$color-danger:                hsl(2, 64%, 58%);
$color-warning:               hsl(35, 84%, 62%);

//$color-vk:                    #4a76a8;
//$color-fb:                    #4267b2;
//$color-tw:                    #40A2F5;
//$color-gh:                    #24292e;


// Семантические цвета

$main-color:                  #5ba060;
$color-main:                  $main-color;
$secondary-color:             #4c7552;
$title-color:                 #383d3d;
$text-color:                  #434749;
$text-muted:                  #a5a5a5;

$body-bg:                     #ffffff;
$block-bg:                    #f2f2f2;

$link-color:                  $main-color;
//$link-color--hov:                  $main-color;

$border-color:                #e8eaeb;

// Базовая типографика

$font-size:                   16px;

$font-size--h1:               28px;
$font-size--h2:               26px;
$font-size--h3:               22px;
$font-size--h4:               18px;
$font-size--h5:               16px;
//$font-size--h6:               16px;

$font-size-sm:                12px;

$line-height:                 1.6;

//$typo-margin-vertical:        1em;

$font-family:                -apple-system, BlinkMacSystemFont, 'Montserrat', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif;
//$font-family--headings:      -apple-system, BlinkMacSystemFont, 'Roboto', 'Ubuntu', 'Droid Sans', 'Helvetica Neue', 'Arial', sans-serif;
$font-family--monospace:     SFMono-Regular, Menlo, Monaco, Consolas, Liberation Mono, Courier New, monospace;


// Ширины

$screen-xs:                   0;
$screen-sm:                   480px;
$screen-md:                   768px;
$screen-lg:                   992px;
$screen-xl:                   1280px;
$screen-xxl:                  1800px;

$container-sm:                100%;
$container-md:                100%;
$container-lg:                100%;
$container-xl:                $screen-xl - 20;
$container-xxl:               $screen-xxl - 20;


// Модульная сетка

$grid-columns:                12;
$grid-gutter-width:           40px;



// Разное

$field-padding-vertical:      6px;
$field-padding-horizontal:    20px;

$border-radius:               0;
$opacity:                     0.7;
$transition-time:             0.3s;
$shadow:                      0 4px 2px -2px rgba(0, 0, 0, 0.3);

$menu-desktop-width:          $screen-xl;
$toggler-part-height:         3px;
$toggler-part-bg-color:       #000;

$z-index-modal:               100;
