body {
  // ТОЛЬКО типографика!
  font-family: $font-family;
  font-size: $font-size;
  line-height: $line-height;
  word-break: break-word;
  text-align: left;
  color: $text-color;
  background-color: $body-bg;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  // text-rendering: optimizeLegibility;
  // Partial support in Android browser versions is due to a serious bug where text-rendering: optimizeLegibility causes custom web fonts to not render!!!
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: $font-family;
  font-weight: 700;
  line-height: 1.2;
  color: $title-color;
}

.h2,
.h3,
.h4,
h4,
h2,
h3 {
  margin-top: 30px;
  margin-bottom: 20px;

  @media (min-width: $screen-md) {
    margin-top: 40px;
  }
}

.h5,
.h6,
h5,
h6 {
  margin-top: 30px;
  margin-bottom: 10px;

  @media (min-width: $screen-md) {
    margin-top: 40px;
  }
}

.h1,
h1 {
  font-size: $font-size--h1;

  @media (min-width: $screen-md) {
    font-size: $font-size--h1 * 1.5;
  }
}

.h2,
h2 {
  font-size: $font-size--h2;

  @media (min-width: $screen-md) {
    font-size: $font-size--h2 * 1.23;
  }
}

.h3,
h3 {
  font-size: $font-size--h3;

  @media (min-width: $screen-md) {
    font-size: $font-size--h3 * 1.091;
  }
}

.h4,
h4 {
  font-size: $font-size--h4;

  @media (min-width: $screen-md) {
    font-size: $font-size--h4 * 1.111;
  }
}

.h5,
.h6,
h5,
h6 {
  font-size: $font-size--h5;
}

.h1--underlined,
.h2--underlined {
  position: relative;
  display: inline-block;
  vertical-align: baseline;
  margin-top: 0;

  @media (min-width: $screen-xl) {
    padding: 0 40px;
    margin-bottom: 40px;
  }

  &::after {
    display: block;
    content: "";
    position: absolute;
    top: 18px;
    left: 0;
    right: 0;
    height: 8px;
    background-color: $main-color;

    @media (min-width: $screen-md) {
      top: 32px;
    }
  }

  span {
    position: relative;
    z-index: 1;
  }
}

.h2--underlined {

  &::after {
    top: 16px;

    @media (min-width: $screen-md) {
      top: 22px;
    }
  }
}

.p,
p,
ul,
ol,
dl,
blockquote,
pre,
address,
figure {
  margin-top: 0;
  margin-bottom: 0.5em;
  color: inherit;

  &:last-child {
    margin-bottom: 0;
  }

  & + & {
    margin-top: 0.5em;
  }
}

address {
  font-style: normal;
}

ul,
ol {
  padding-left: 0;
}

ol {
  //padding-left: 1em;
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-top: 0.5em;
  margin-bottom: 0;
  margin-left: 1.3em;
}

li {
  line-height: inherit;
}

li + li {
  margin-top: 0.5em;
}

dl {

  dd {
    margin-left: 0;
  }
}

hr {
  box-sizing: content-box; // Проблема Firefox
  height: 0;               // Проблема Firefox
  overflow: visible;       // Проблема Edge и IE
  margin-top: 1em;
  margin-bottom: 1em;
  border: 0;
  border-top: 1px solid $border-color;
}

.small,
small {
  font-size: $font-size * 0.75;
}

sub,
sup {
  font-size: 70%;
}

b,
strong {
  font-weight: bolder;
}

abbr[title] {
  text-decoration: underline dotted;
}

blockquote {
  padding: 0;
  margin-left: 0;
  margin-right: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-left: 0;

  @media (min-width: $screen-sm) {
    margin-left: 1.5rem;
  }

  & + dt {
    margin-top: 0.5em;
  }
}

// samp,
code,
kbd,
pre {
  // font-family: $font-family--monospace;
}

code {
  padding: 0.06em 0.3em;
  color: $text-color;
  background-color: rgba(#000, 0.08);
  border-radius: $border-radius;
}

kbd {
  padding: 0.06em 0.3em;
  color: $text-color;
  background-color: $gray-lightest;
  border-radius: $border-radius;

  kbd {
    padding: 0;
  }
}

pre {
  display: block;
  width: 100%;
  margin-bottom: 0.5em;
  overflow-x: auto;
  tab-size: 2;
  background-color: rgba(#000, 0.08);

  code {
    background: transparent;
  }
}

ol {
  list-style: none;
  counter-reset: myCounter;

  & > li::before {
    display: inline-block;
    vertical-align: baseline;
    min-width: 22px;
    margin-right: 5px;
    counter-increment: myCounter;
    content: counter(myCounter)".";
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    text-align: left;
    line-height: inherit;
    color: $title-color;
  }
}

ul {
  list-style: none;

  & > li::before {
    display: inline-block;
    vertical-align: middle;
    content: "";
    width: 6px;
    height: 6px;
    margin-right: 20px;
    margin-top: -2.5px;
    border-radius: 50%;
    background-color: $main-color;
  }
}
